@font-face {
  src: local(HealB), url("HealTheWebB-Regular.b4c2386d.otf");
  font-family: HealB;
  src: local(HealA), url("HealTheWebA-Regular.74330abb.otf");
  font-family: HealA;
}

:root {
  font-family: HealA;
  font-size: 62.5%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: HealA;
}

header {
  height: 35vh;
  border-bottom: 2px solid #000;
  justify-content: center;
  align-items: center;
  display: flex;
}

main {
  height: 100vh;
  width: 100vw;
  background-color: pink;
}

.menu {
  margin-bottom: 2vh;
}

.dropdwn_btn {
  color: #000;
  width: 5vw;
  background: none;
  border: none;
  font-size: 4rem;
  transition: all 4s;
  animation: eye 13s infinite;
}

@keyframes eye {
  0% {
    transform: rotate(0deg);
  }

  50% {
    transform: rotateY(360deg);
  }

  100% {
    transform: rotatX(180deg);
  }
}

.dropdown_cont {
  color: #000;
  min-height: 5.6rem;
  display: none;
  position: relative;
  left: 1rem;
}

.dropdown_cont a {
  float: none;
  color: #000;
  text-align: left;
  padding: .2rem .6rem;
  text-decoration: none;
  display: block;
}

.dropdown_cont a:hover {
  color: #fff;
  background-color: #000;
  border-radius: 68px 52px 22px 68px / 91px 67px 73px 29px;
}

.dropdown:hover .dropdown_cont {
  width: 10rem;
  display: block;
}

.dropdown:hover .dropdwn_btn {
  display: none;
}

.top {
  text-align: center;
}

.top h1 {
  font-size: 8rem;
}

.form {
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.form label {
  margin: 1rem;
}

.form input {
  width: 90%;
}

.form input:focus {
  border: 2px solid #ff69b4;
  border-radius: 91% 8% 92% / 92% 3% 97%;
}

.form textarea {
  width: 90%;
  height: 5rem;
  resize: none;
  margin: 1rem;
}

